import face from './face';

const videoElement = document.querySelector('video');
const info = document.getElementById('info');
const gameui = document.getElementById('gameui');
const lostui = document.getElementById('lostui');
const retryBt = document.getElementById('retry');
const timerend = document.getElementById('timefinal');

let lost=false;

retryBt.addEventListener("click",()=>{
  lostui.style.display="none"
  gameui.style.display="flex"

  lost=false;

  timeStart = new Date().getTime();
})

function changeColor(){
    document.body.style.backgroundColor = '#' + Math.floor(Math.random() * 16777215).toString(16);;
}

let timeStart;

const init = async () => {

    info.innerHTML = "Loading...";
    await face.loadModel();

    info.innerHTML = "Accessing Camera...";
    try{
      await face.setUpCamera(videoElement);
    }catch(err){
      console.log(err.message)
      info.innerHTML ="Camera "+ err.message.toLowerCase()+" :/ <br> Fixx the problem and refresh the page.";
      face.stopPrediction();
      document.getElementById('facedet').style.display="none";
      return;
    }

    timeStart = new Date().getTime();

    info.innerHTML = "";
    gameui.style.display="flex"

    const predict = async () => {
        let result = await face.getBlinkPrediction();
        //model ready
    
        if (result) {
          // if (result.blink) {
          //   blinkIndicator.style.color = 'red';
          // } else {
          //   blinkIndicator.style.color = 'green';
          // }
          if (result.longBlink) {
           
            console.log("blink")
            //changeColor()
            gameui.style.display="none"
            lostui.style.display="flex"

            if(lost==false){
              timerend.innerHTML=getTimer().seconds+"."+getTimer().ms.slice(0, 1)+"s";
            }
            lost=true;
          }
        }
        requestAnimationFrame(predict);
      };
      predict();
};

function getTimer() {
  return {
      /** <integer>s e.g 2s etc. */
      get seconds() {
          const seconds = Math.ceil((new Date().getTime() - timeStart) / 1000) ;
          return seconds;
      },
      /** Milliseconds e.g. 2000ms etc. */
      get ms() {
          const ms = (new Date().getTime() - timeStart) + 'ms';
          return ms;
      }
  }
}


init()